import { useRef, useState } from 'react';
import Slider from 'rc-slider';
import YouTube from 'react-youtube';
import {
  FaSpinner,
  FaPlay,
  FaPause,
  FaStepBackward,
  FaStepForward,
} from 'react-icons/fa';
import { TiArrowShuffle } from 'react-icons/ti';
import { FiRepeat } from 'react-icons/fi';

const opts = {
  height: '0',
  width: '0',
  playerVars: {
    autoplay: 1,
  },
};

const YT_STATE = {
  UNSTARTED: -1,
  ENDED: 0,
  PLAYING: 1,
  PAUSED: 2,
  BUFFERING: 3,
  CUED: 5,
};

const renderPlayButton = (state) => {
  const classes = 'mx-4 cursor-pointer text-white';
  switch (state) {
    case YT_STATE.PLAYING:
      return <FaPause className={classes}></FaPause>;
    case YT_STATE.PAUSED:
      return <FaPlay className={classes}></FaPlay>;
    default:
      return (
        <FaSpinner className="pointer-events-none mx-4 text-white icon-spin"></FaSpinner>
      );
  }
};

const Player = ({ id }) => {
  const ref = useRef();

  const [isRepeat, setIsRepeat] = useState(false);
  const [isShuffle, setIsShuffle] = useState(false);
  const [ytState, setYtState] = useState(YT_STATE.UNSTARTED);

  const onStateChange = (event) => {
    switch (event.data) {
      case YT_STATE.PLAYING:
        setYtState(YT_STATE.PLAYING);
        break;
      case YT_STATE.PLAYING:
        setYtState(YT_STATE.PLAYING);
        break;
      default:
        break;
    }
  };

  return (
    <>
      <YouTube
        ref={ref}
        videoId={id}
        opts={opts}
        onStateChange={onStateChange}
      />
      <div className="ml-16 flex">
        <div className="flex items-center">
          <FiRepeat
            className={`mx-4 cursor-pointer ${
              isRepeat ? 'text-primary' : 'text-white'
            }`}
            onClick={() => {
              setIsRepeat(!isRepeat);
            }}
          />
          <FaStepBackward className="mx-4 cursor-pointer text-white" />
          {renderPlayButton(ytState)}
          <FaStepForward className="mx-4 cursor-pointer text-white" />
          <TiArrowShuffle
            className={`mx-4 cursor-pointer text-xl ${
              isShuffle ? 'text-primary' : 'text-white'
            }`}
            onClick={() => {
              setIsShuffle(!isShuffle);
            }}
          />
        </div>
        <div>
          <Slider min={0} max={100} defaultValue={3} />
        </div>
      </div>
    </>
  );
};

export default Player;
